<template>
  <section class="play-page">
    <!-- ----------------------------------------------背景图----------------------------------------------------- -->
    <div
      class="mask"
      :style="{
        backgroundImage: `url(${
          currentId.song ? currentId.picUrl : currentId.al.picUrl
        }?imageView=1&type=webp&thumbnail=246x0)`,
      }"
    ></div>
    <div class="pull" @click="$emit('toggle-show-play-page', false)">
      <img src="@/assets/song/下拉.png" />
    </div>
    <!-- ------------------------------------------------旋转唱片 ----------------------------------------------------->
    <section class="rotate" v-show="!showlyrics" @click="showlyrics = true">
      <img
        src="https://s3.music.126.net/mobile-new/img/needle-ab.png"
        class="needle"
        :class="{ paused: playing }"
      />
      <section class="rotated" :class="{ playing: playing }">
        <img
          :src="
            `${
              currentId.song ? currentId.picUrl : currentId.al.picUrl
            }?imageView=1&type=webp&thumbnail=246x0`
          "
          class="thumb"
        />
        <img
          src="https://s3.music.126.net/mobile-new/img/disc.png"
          class="disc"
        />
      </section>
    </section>
    <!-- -----------------------------------------------歌词页 ------------------------------------------------------->
    <section
      class="lyrics"
      v-show="showlyrics"
      @click="showlyrics = false"
      ref="lyrics"
    >
      <ul
        class="content"
        ref="lyricContent"
        
        :style="{ marginTop: -scollHi + 'px' }"
      >
        <li v-for="(ly, index) in analysisLyric" :key="index">
          <span
            :class="{ active: currentLyIndex === index }"
            :style="{
              animationDuration: analysisLyric[index + 1]
                ? analysisLyric[index + 1].time - ly.time - 0.5 + 's'
                : '3s',
            }"
            >{{ ly.text }}</span
          >
        </li>
      </ul>
    </section>
    <!-- 进度条 -->
    <section class="progress">
      <input
        type="range"
        :max="duration"
        step="0.5"
        v-model="value"
        @change="progressChange"
        @input="progressInpupt"
      />
    </section>
    <!-- -----------------------------------------------播放器按钮--------------------------------------------------- -->
    <div class="controls">
      <div>
        <img src="@/assets/顺序播放.png" />
      </div>
      <div @click="$emit('prve-song')">
        <img src="@/assets/上一曲.png" />
      </div>
      <div @click="$emit('toggle-playing-start')">
        <img src="@/assets/播放中.png" />
      </div>
      <div @click="$emit('next-song')">
        <img src="@/assets/下一曲.png" />
      </div>
      <div @click="$emit('toggle-show-play-list', true)">
        <img src="@/assets/播放队列.png" />
      </div>
    </div>
  </section>
</template>

<script>
// import Lyric from "./Lyric.vue";
export default {
  components: {
    // Lyric,
  },
  props: {
    currentId: Object,
    playing: Boolean,
    currentTime: Number,
    duration: Number,
  },
  data() {
    return {
      inputing: false,
      value: this.currentTime,
      showlyrics: false,
      lyric: null,
      scollHi: 0,
      liHeight: [],
    };
  },
  created() {
    this.getLyric(this.currentId.id);
    // console.log(this.$refs.lyricContent);
  },
  methods: {
    progressChange(event) {
      this.inputing = false;
      this.$emit("change-progress-value", event.target.value);
    },
    progressInpupt() {
      this.inputing = true;
    },
    getLyric(id) {
      this.axios
        .get("http://apis.netstart.cn/music/lyric", {
          params: { id },
        })
        .then((res) => {
          this.lyric = res.data.lrc.lyric;
        });
    },
  },
  watch: {
    currentTime(n) {
      if (!this.inputing) {
        this.value = n;
      }
    },
    analysisLyric() {
      //监听下一次变化
      // console.log("analysisLyric", this.$refs.lyricContent);
      if (this.analysisLyric) {
        this.$nextTick(() => {
          // console.log("nextTick", this.$refs.lyricContent);
          var lis = this.$refs.lyricContent.querySelectorAll("li");

          this.liHeight = [...lis].map((item) => item.offsetHeight);
        });
      } else {
        this.liHeight = [];
      }
    },
    currentLyIndex(index) {
      console.log(index);
      // console.log(this.liHeight.slice(0, index));

      var h = this.liHeight.slice(0, index).reduce(function(total, item) {
        console.log(total, item, total + item);
        return total + item;
      }, 0);
      console.log(h);
      h = h > 174 ? h - 174 : 0;
      // h = h > 1482 ? 1482 : h;
      this.scollHi = h;
    },
    "currentId.id"(id) {
      this.getLyric(id);
    },
  },
  computed: {
    analysisLyric() {
      if (this.lyric) {
        return this.lyric
          .split("\n")
          .filter((s) => s)
          .map((s) => {
            let text = s.replace(/^\[\d{0,2}:\d{0,2}.\d{0,3}\]/i, "");
            let timeStr = s.replace(text, "").replace(/(^\[|\]$)/g, "");
            let timeArr = timeStr.split(":").map((item) => Number(item));
            let time = timeArr[0] * 60 + timeArr[1];
            return { text, time };
          });
      } else {
        return [];
      }
    },
    currentLyIndex() {
      var i = this.analysisLyric.findIndex(
        (item) => item.time > this.currentTime
      );
      var currentLyricIndex = i !== -1 ? i - 1 : this.analysisLyric.length - 1;
      return currentLyricIndex;
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.play-page {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: rgb(35, 44, 48);
  z-index: 1;

  .mask {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    // z-index: -1;
    filter: blur(30px) brightness(0.8);
    transform: scale(1.5);
  }
  .pull {
    width: 30px;
    height: 30px;
    position: absolute;
    top: 15px;
    left: 15px;
    z-index: 1;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .rotate {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    // background: chartreuse;
    img.needle {
      width: 85px;
      height: 145px;
      position: absolute;
      top: 15px;
      left: 45%;
      z-index: 1;
      transform-origin: 15px 15px;
      transform: rotate(320deg);
      transition: all 3s;
      &.paused {
        transform: rotate(350deg);
      }
    }
    .rotated {
      position: relative;
      width: 80vw;
      height: 80vw;
      margin: 75px auto 0 auto;
      animation: rotate 10s linear infinite;
      animation-play-state: paused;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 80vw;
        height: 80vw;
        border-radius: 50%;
        &.thumb {
          transform: scale(0.7);
        }
      }
      &.playing {
        animation-play-state: running;
      }
    }
  }
  .lyrics {
    width: 75vw;
    height: 50vh;
    text-align: center;
    background: rgba(255, 255, 255, 0);
    position: absolute;
    top: 90px;
    left: 13%;
    overflow: hidden;
    z-index: 999;
    ul {
      transition: all 0.3s;
      li {
        line-height: 1.8;
        font-size: 19px;
        span {
          &.active {
            color: red;
          }
        }
      }
    }
  }
  .progress {
    position: absolute;
    padding: 20px 0;
    left: 10%;
    bottom: 200px;
    width: 100%;
    z-index: 999;
    input {
      width: 80%;
      height: 20px;
    }
  }
  .controls {
    position: absolute;
    bottom: 150px;
    left: 0;
    width: 100%;
    height: 10vh;
    // background: chartreuse;
    display: flex;
    justify-content: space-around;
    align-items: center;
    div {
      width: 48px;
      height: 48px;
      font-size: 20px;
      text-align: center;
      padding-top: 5px;
      box-sizing: border-box;
      //   border: 1px solid black;
      img {
        width: 100%;
        // height: 100%;
      }
    }
  }
}
</style>
