<template>
  <section class="play-list">
    <!-- 遮罩层 -->
    <div
      class="mask animate__animated animate__slideInUp"
      @click="$emit('toggle-show-play-list', false)"
    ></div>
    <!-- 包裹层 -->
    <div class="swiper">
      <!-- 卡片 -->
      <div class="card animate__animated animate__slideInUp">
        <ul class="list">
          <SongList
            v-for="(item, index) in songplayList"
            :key="item.id"
            :item="item"
            :playing="playing"
            @song-id="$emit('song-id', $event)"
            :currentId="currentId.id"
          >
            <span>{{ (index + 1).toString().padStart(2, "0") }}</span>
          </SongList>
        </ul>
      </div>
    </div>
  </section>
</template>

<script>
import SongList from "../components/SongList.vue";
export default {
  components: {
    SongList,
  },
  props: {
    songplayList: Array,
    playing: Boolean,
    currentId: Object,
  },
};
</script>

<style lang="less" scoped>
.play-list {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  .mask {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    left: 0;
    top: 0;
  }
  .swiper {
    width: 100vw;
    height: 60vh;
    //   background: rgba(255, 0, 0, 0.945);
    position: absolute;
    left: 0;
    bottom: 10px;

    padding: 0 10px;
    box-sizing: border-box;
    .card {
      // background: rgba(0, 255, 242, 0.39);
      background-color: white;
      height: 100%;
      border-radius: 15px;
      padding: 15px 0;
      box-sizing: border-box;
      transition: all 0.3s;
      ul.list {
        display: block;
        height: 100%;
        overflow-y: auto;
        &::-webkit-scrollbar {
          width: 6px;
          background: #999;
        }
        &::-webkit-scrollbar-track {
          background-color: #60615edc;
        }
        &::-webkit-scrollbar-thumb {
          background-color: #ffeb3b;
        }
      }
    }
  }
}
</style>
